.model__stage {
  font-family: 'Computer Modern Sans';
  font-size: 24px; }
  .model__stage:hover {
    cursor: pointer;
    opacity: 0.5; }

.model__dimension {
  font-family: 'Computer Modern Sans';
  font-size: 22px; }

.model__stage-rect {
  shape-rendering: crispEdges; }

.model__icon {
  opacity: 0.25; }
  .model__icon--chart {
    -webkit-transform: translate(-29px, 269px) scale(0.35);
        -ms-transform: translate(-29px, 269px) scale(0.35);
            transform: translate(-29px, 269px) scale(0.35); }
  .model__icon--magnify {
    -webkit-transform: translate(180px, 270px) scale(0.7);
        -ms-transform: translate(180px, 270px) scale(0.7);
            transform: translate(180px, 270px) scale(0.7); }
  .model__icon--gear {
    -webkit-transform: translate(330px, 240px) scale(0.3);
        -ms-transform: translate(330px, 240px) scale(0.3);
            transform: translate(330px, 240px) scale(0.3); }
  .model__icon--fist {
    -webkit-transform: translate(480px, 200px) scale(7);
        -ms-transform: translate(480px, 200px) scale(7);
            transform: translate(480px, 200px) scale(7); }
  .model__icon--bulb {
    -webkit-transform: translate(646px, 134px) scale(0.35);
        -ms-transform: translate(646px, 134px) scale(0.35);
            transform: translate(646px, 134px) scale(0.35); }
  .model__icon path {
    fill: #fff; }

.model__name {
  font-size: 42px; }



