.dimension {
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  opacity: 0; }
  .dimension--entering {
    opacity: 0; }
  .dimension--entered {
    opacity: 1; }
  .dimension__name {
    text-align: right;
    font-weight: bold;
    padding: 10px 0; }
  .dimension__description {
    border-bottom: 1px solid; }
  .dimension__section {
    border-bottom: 1px solid;
    padding: 10px 0 20px 0; }



